@import '../../styles/mixins.scss';

.texteditor {
    @include form-field;
    min-height: 25rem;

    p[data-placeholder]:first-child::before {
        content: attr(data-placeholder);
        font-size: 1rem;
        color: var(--dark-text);
    }

    ul, ol {
        margin-left: 1.5rem;
    }

    code {
        font-family: 'Courier New', Courier, monospace;
        color: var(--accent-second-contrast)
    }
}

.texteditor__textfield {
    padding: 1rem;
    min-height: 15rem;
}

.texteditor__previewfield {
    padding: 1rem;
    min-height: 8rem;
    border-bottom: 2px solid var(--dark-text);
}