.menubar{
    padding: 0.3rem;
    border-radius: 0.3rem;
    border-bottom: 2px solid var(--dark-text);

    button {
        padding: 0.5rem;
        margin: 0.2rem;
        font-size: 1rem;
        color: var(--bright-text);
        background-color: var(--content-bg-light);
        border: 2px solid var(--dark-text);
        border-radius: 0.5rem;
    }

    .active {
        background-color: var(--accent-second);
    }
}

